*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
