.header {
  flex: 0 0 auto; }
  .header .header__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 25px; }
    .header .header__wrapper .header__logo {
      display: block; }
    .header .header__wrapper .header__menu_burger {
      display: none; }
    .header .header__wrapper .header__menu_close {
      display: none; }
    .header .header__wrapper .header__nav .header__nav_item {
      font-weight: 500;
      font-size: 14px;
      line-height: 14px; }
      .header .header__wrapper .header__nav .header__nav_item:hover {
        color: #2c39f2;
        transition: color 5ms; }
    .header .header__wrapper .header__nav .header__nav_item + .header__nav_item {
      margin-left: 24px; }
    .header .header__wrapper .header__nav .nav_sup {
      top: -.3em;
