      .header .header__wrapper .header__menu_burger .line {
        fill: none;
        stroke: #2c39f2;
        stroke-width: 6;
        transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1); }
        .header .header__wrapper .header__menu_burger .line.line1 {
          stroke-dasharray: 60 207;
          stroke-width: 6; }
        .header .header__wrapper .header__menu_burger .line.line2 {
          stroke-dasharray: 60 60;
          stroke-width: 6; }
        .header .header__wrapper .header__menu_burger .line.line3 {
          stroke-dasharray: 60 207;
