.preloader__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: 100vh;
  background-color: #ffffff;
  transition: left 0.5s;
  position: fixed;
  left: -100%;
  z-index: 1;
  width: 100%; }
  .preloader__wrapper.active {
    left: 0; }
  .preloader__wrapper .preloader__image {
    height: 100%;
    opacity: 0;
    transition: opacity 2s;
    transition-delay: 0.5s; }
    .preloader__wrapper .preloader__image.active {
